.tippy-box[data-animation="fade"][data-state="hidden"] {
  opacity: 0;
}

[data-tippy-root] {
  max-width: calc(100vw - 10px);
}

.tippy-box {
  color: #fff;
  white-space: normal;
  background-color: #333;
  border-radius: 4px;
  outline: 0;
  font-size: 14px;
  line-height: 1.4;
  transition-property: -webkit-transform, transform, visibility, opacity;
  position: relative;
}

.tippy-box[data-placement^="top"] > .tippy-arrow {
  bottom: 0;
}

.tippy-box[data-placement^="top"] > .tippy-arrow:before {
  border-width: 8px 8px 0;
  border-top-color: initial;
  -webkit-transform-origin: top;
  transform-origin: top;
  bottom: -7px;
  left: 0;
}

.tippy-box[data-placement^="bottom"] > .tippy-arrow {
  top: 0;
}

.tippy-box[data-placement^="bottom"] > .tippy-arrow:before {
  border-width: 0 8px 8px;
  border-bottom-color: initial;
  -webkit-transform-origin: bottom;
  transform-origin: bottom;
  top: -7px;
  left: 0;
}

.tippy-box[data-placement^="left"] > .tippy-arrow {
  right: 0;
}

.tippy-box[data-placement^="left"] > .tippy-arrow:before {
  border-width: 8px 0 8px 8px;
  border-left-color: initial;
  -webkit-transform-origin: 0;
  transform-origin: 0;
  right: -7px;
}

.tippy-box[data-placement^="right"] > .tippy-arrow {
  left: 0;
}

.tippy-box[data-placement^="right"] > .tippy-arrow:before {
  border-width: 8px 8px 8px 0;
  border-right-color: initial;
  -webkit-transform-origin: 100%;
  transform-origin: 100%;
  left: -7px;
}

.tippy-box[data-inertia][data-state="visible"] {
  transition-timing-function: cubic-bezier(.54, 1.5, .38, 1.11);
}

.tippy-arrow {
  width: 16px;
  height: 16px;
  color: #333;
}

.tippy-arrow:before {
  content: "";
  border-style: solid;
  border-color: rgba(0, 0, 0, 0);
  position: absolute;
}

.tippy-content {
  z-index: 1;
  padding: 5px 9px;
  position: relative;
}

.zoodb-search-widget .search-input {
  box-sizing: border-box;
  width: 100%;
  padding: .5em 1em;
  font-size: 1.2rem;
}

.zoodb-search-widget .search-no-results, .zoodb-search-widget .search-no-query-string, .zoodb-search-widget .search-results-error {
  padding: 2em 0;
}

.zoodb-search-widget .search-no-query-string {
  font-style: italic;
}

.zoodb-search-widget .search-results-error {
  color: #b40000;
  padding: 2em 0;
}

.zoodb-search-widget .search-query-info {
  margin-top: .5em;
  display: block;
}

.zoodb-search-widget .search-result .search-result-title {
  font-style: bold;
  display: block;
}

.zoodb-search-widget .search-result .search-result-display-field-name {
  color: rgba(0, 0, 0, .6);
  font-size: .9em;
}

.zoodb-search-widget .search-result .search-result-display-field-name:before {
  content: " — ";
}

.zoodb-search-widget .search-result .search-result-field-display {
  font-style: italic;
  display: block;
}

.zoodb-search-widget .search-instructions-tip p {
  margin: .4em 0;
  font-size: 14px;
  font-style: italic;
}

.zoodb-search-widget .search-instructions-tip p.search-instructions-item:before {
  content: "• ";
}

.zoodb-search-widget .search-instructions-tip code {
  font-family: monospace;
  font-size: 12.5px;
}

.sr-highlight {
  background-color: rgba(255, 255, 200, .5);
}

.tippy-box[data-theme~="light"] {
  color: #26323d;
  background-color: #fff;
  box-shadow: 0 0 20px 4px rgba(154, 161, 177, .15), 0 4px 80px -8px rgba(36, 40, 47, .25), 0 4px 4px -2px rgba(91, 94, 105, .15);
}

.tippy-box[data-theme~="light"][data-placement^="top"] > .tippy-arrow:before {
  border-top-color: #fff;
}

.tippy-box[data-theme~="light"][data-placement^="bottom"] > .tippy-arrow:before {
  border-bottom-color: #fff;
}

.tippy-box[data-theme~="light"][data-placement^="left"] > .tippy-arrow:before {
  border-left-color: #fff;
}

.tippy-box[data-theme~="light"][data-placement^="right"] > .tippy-arrow:before {
  border-right-color: #fff;
}

.tippy-box[data-theme~="light"] > .tippy-backdrop {
  background-color: #fff;
}

.tippy-box[data-theme~="light"] > .tippy-svg-arrow {
  fill: #fff;
}

